.uBlock {
    border: 1px solid #98FF98;
    width: 500px;
    height: 150px;
    display: grid;
    grid-template-columns: 1fr 3fr 2fr;
    grid-template-rows: 30px 90px 30px;
    column-gap: 20px;
}

.avatar {
    grid-area: span 3 / 1;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    align-self: center;
}

.blockName {
    grid-area: 2 / 2;
}
.btn {
    grid-area: 2 / 3;
    height: 30px;
    align-self: center;
    color: gray;
    background-color: antiquewhite;
    border-radius: 20px;
    border: 0;
    outline: none;
}
.number {
    border: 1px solid red;
    margin: 3px;
    cursor: pointer;
}
.numberActive {
    color: gold;
    cursor: pointer;
}